	cursor: default;
}

#image_container {
	display: none;
	width: 100%;
	height: 100%;
	z-index: 1002;
	background-color: white;
	position: fixed;
	padding: 30px;
	border: 1px solid black;
	background: rgb(0, 0, 0);
	/* other browser support */
	background: rgba(0, 0, 0, 0.9);
}

.transp_black {
	display: none;
	width: 100%;
	height: 100%;
	position: fixed;
	background-color: black;
	opacity: 0.5;
