{
	/*  ------------------ MENU ----------- */
}

.menu_container {
	display: none;
	right: -1%;
	position: fixed;
	top: 16%;
	width: 320px;
	height: 500px;
	z-index: 1005;
}

.wrap_hamb {
	right: 4%;
	height: 50px;
	width: 50px;
	position: fixed;
	top: 12%;
	z-index: 1003;
	transition: 0.8s linear;
	opacity: 1;
}

.hamb {
	position: relative;
	margin-top: 6%;
	margin-right: 0;
	float: right;
	font-size: 35px;
	color: black;
	font-weight: bold;
	cursor: pointer;
}

.close_menu {
	font-size: 30px;
	left: 5%;
	cursor: pointer;
	position: absolute;
	font-weight: bold;
}

.close_menu:hover {
	opacity: 0.7;
}

.menu {
	position: absolute;
	z-index: 1002;
	box-shadow: -5px 0 5px -5px #333;
	height: 400px;
	width: 0;
	margin-top: 40px;
	display: -webkit-inline-flex;
	display: inline-flex;
	color: black;
	right: 0;
	background-color: white;
	transition: 0.5s;
}

.p_menu {
	margin-top: 20%;
	margin-left: 14%;
}

.menu p {
	width: 220px;
	text-align: center;
	list-style-type: none;
	margin-top: 20%;
	cursor: pointer;
	padding: 3px;
	transition: 0.5s;
	letter-spacing: 2px;
	font-family: 'Courgette', cursive;
	color: black;
	border-bottom: 1px solid black;
}

.menu p a {
	color: black;
}

.menu p a:hover {
	color: black;
}

.menu p:hover {
	background-color: black;
	color: white;
	transition: 0.1s;
	text-decoration: none;
}

.menu .before_menu_one:before {
	position: absolute;
	content: "";
	top: -24.6px;
	left: 0;
	width: 340px;
	padding: 10px;
	height: 50px;
	background: white;
	box-shadow: 0 -5px 5px -5px #333;
	z-index: -1;
	-webkit-transform: rotate(-2.6deg);
	transform: rotate(-8deg);
	outline: 5px solid transparent;
}

.menu .before_menu_two:before {
	position: absolute;
	content: "";
	top: 360px;
	left: 1%;
	width: 320px;
	padding: 10px;
	height: 70px;
	background: white;
	z-index: -1;
	box-shadow: 0 5px 5px -5px #333;
	-webkit-transform: rotate(-2.6deg);
	transform: rotate(10deg);
	outline: 5px solid transparent;
}

