

html {
	overflow-y: scroll;
	overflow-x: hidden;
	height: 100%;
}

body {
	margin: 0;
	padding: 0;
	overflow: hidden;
	transition: background-color .5s;
}

.container_images {
	height: auto;
	overflow: auto;
	background-image: url('./images/photographer-1149781_1920.jpg');
	background-size: contain;
	background-repeat: no-repeat;
	background-position: 100% -20%;
}

.wrap_images {
	float: right;
	padding: 5%;
	margin-top: 20%;
	background-color: white;
}

.clear {
	clear: both;
}

.img_cont {
	min-height: 250px;
	min-width: 280px;
	width: auto;
	position: relative;
	margin: 3px;
	overflow: hidden !important;
	-webkit-user-select: none;
	/* webkit (safari, chrome) browsers */
	-moz-user-select: none;
	/* mozilla browsers */
	-khtml-user-select: none;
	/* webkit (konqueror) browsers */
	-ms-user-select: none;
	/* IE10+ */
}

.img_span {
	height: 100%;
	width: 100%;
	position: absolute;
	transition: 1s all ease;
}

.img_span img {
	width: 100%;
	height: 100%;
}

.img_span:hover {
	transform: scale(1.05);
	opacity: 0.8;
}

