	/* ------------- VIEW IMAGE ---------- */
}

.lb-data .lb-close {
	background-image: url('./images/close.png') !important;
}

.lb-data .lb-number {
	display: none !important;
}

.lb-nav a.lb-prev {
	background: url('./images/prev.png') left 48% no-repeat !important;
}

.lb-nav a.lb-next {
	background: url('./images/next.png') right 48% no-repeat !important;
}

.lb-data .lb-close {
	background: url('./images/close.png') top right no-repeat !important;
}

.lb-cancel {
	background: url('./images/loading.gif') no-repeat;
}

.lb-container {
	-webkit-user-select: none;
	/* webkit (safari, chrome) browsers */
	-moz-user-select: none;
	/* mozilla browsers */
	-khtml-user-select: none;
	/* webkit (konqueror) browsers */
	-ms-user-select: none;
	/* IE10+ */
}

@media screen and (min-width: 200px) and (max-width: 575px) {
	.container_images {
		margin-top: 50px;
		background-size: contain;
		background-position: center top
	}
	.wrap_images {
		margin-top: 60%;
	}
}

@media screen and (min-width: 575px) and (max-width: 1375px) {
	.container_images {
		margin-top: 30px;
		background-size: contain;
		background-position: center top
	}
	.wrap_images {
		margin: 30%;
	}
}


/* ------- MICROSOFT EDGE -------- */

@supports (-ms-ime-align:auto) {
	.wrap_images {
		margin-top: 330px !important;
		padding-top: 50px !important;
	}
}

